// MIXINS



@mixin icon-bubble( $bubble:false ) {
  // Position
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  position: relative;
  margin: 0;
  // Height & border
  width: 35px;
  height: auto;
  // Font
  font-family: $icon-font;
  font-size: 20px;
  line-height: normal;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // Colors
  color: $link-active-color;
  top: -2px;
  @if ($bubble) {
    margin-right: 10px;
    height: 35px;
    line-height: 33px;
    // top: -3px;
    border-radius: 50%;
    background: #ffffffc7;
  }
}


